@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: local("Open Sans"), url("../fonts/opensanslight.woff2") format("woff2"), url("../fonts/opensanslight.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Open Sans"), url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Open Sans"), url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page {
  height: 100%;
  font-family: "Open Sans", "Arial", sans-serif;
}
.page__body {
  min-height: 100%;
  margin: 0;
  background-color: #ffffff;
  color: #000000;
}
.page-header {
  position: absolute;
  width: 100%;
  min-height: 66px;
  color: #ffffff;
}
.page-header--opened .page-header__wrapper {
  flex-direction: column;
  min-height: 69px;
  background-color: #283645;
}
.page-header--opened .page-header__logo {
  padding-top: 23px;
  padding-bottom: 17px;
}
.page-header--no-js .page-header__logo {
  min-height: 46px;
}
.page-header--no-js {
  position: static;
}
.page-header--no-js .page-header__wrapper {
  flex-direction: column;
  min-height: 69px;
  background-color: #283645;
  border-bottom: 1px solid #000000;
}
.page-header__wrapper {
  display: flex;
  justify-content: space-between;
  min-height: 66px;
  background-color: rgba(0, 0, 0, 0.7);
}
.page-header__logo {
  display: block;
  padding-left: 20px;
  padding-top: 22px;
}
.page-header__logo-img--tablet {
  display: none;
}
.page-header__logo-img--desktop {
  display: none;
}
@media (min-width: 660px) {
  .page-header__wrapper {
    min-height: 110px;
  }
  .page-header--opened {
    border-bottom: 1px solid #000000;
  }
  .page-header--opened .page-header__logo {
    padding-top: 27px;
    padding-bottom: 20px;
    border: none;
  }
  .page-header--no-js .page-header__logo {
    min-height: 59px;
  }
  .page-header__logo {
    width: 145px;
    padding-top: 27px;
  }
  .page-header__logo-img--mobile {
    display: none;
  }
  .page-header__logo-img--tablet {
    display: block;
  }
  .page-header__toggle {
    width: 23px;
    height: 23px;
  }
}
@media (min-width: 1200px) {
  .page-header {
    min-height: 110px;
  }
  .page-header--no-js {
    position: absolute;
  }
  .page-header--no-js .page-header__wrapper,
  .page-header--opened .page-header__wrapper {
    display: flex;
    flex-direction: row;
    min-height: 110px;
    background-color: rgba(0, 0, 0, 0.7);
  }
  .page-header__wrapper {
    padding: 0 130px;
  }
  .page-header__logo {
    width: 147px;
    padding-left: 0;
  }
  .page-header__logo-img--tablet {
    display: none;
  }
  .page-header__logo-img--desktop {
    display: block;
  }
  .page-header__toggle {
    display: none;
  }
}
.main-nav {
  position: relative;
  width: 100%;
  min-height: 66px;
}
.main-nav--closed .main-nav__wrapper {
  display: none;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 21px;
  right: 19px;
  width: 50px;
  height: 4px;
  background-color: #ffffff;
  border-radius: 1px;
  box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff;
}
.main-nav--closed .main-nav__toggle:hover::before {
  background-color: #d22856;
  box-shadow: 0 10px 0 0 #d22856, 0 20px 0 0 #d22856;
}
.main-nav--closed .main-nav__toggle:active::before {
  background-color: rgba(210, 40, 86, 0.3);
  box-shadow: 0 10px 0 0 rgba(210, 40, 86, 0.3), 0 20px 0 0 rgba(210, 40, 86, 0.3);
}
.main-nav--opened .main-nav__wrapper {
  display: block;
  position: absolute;
  width: 100%;
}
.main-nav--opened .main-nav__toggle {
  top: -69px;
  right: 0;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 35px;
  right: 17px;
  width: 30px;
  height: 2px;
  background-color: #ffffff;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:hover::before,
.main-nav--opened .main-nav__toggle:hover::after {
  background-color: #d22856;
}
.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  background-color: rgba(210, 40, 86, 0.3);
}
.main-nav--no-js {
  position: static;
}
.main-nav__wrapper {
  margin: 0 auto;
}
.main-nav__toggle {
  position: absolute;
  right: 0;
  z-index: 2;
  display: block;
  width: 90px;
  height: 66px;
  padding: 0;
  background-color: rgba(0, 0, 0, 0);
  cursor: pointer;
  border: none;
}
.main-nav__list {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-nav__item {
  font-weight: 700;
  font-size: 18px;
  line-height: 32px;
  text-transform: uppercase;
  background-color: #283645;
  min-width: 320px;
  text-align: center;
  padding: 16px 0 15px;
  border-top: 1px solid #000000;
  box-shadow: 0 1px 0 #000000, inset 0 1px 0 rgba(255, 255, 255, 0.15);
}
.main-nav__item:last-child {
  border-bottom: none;
}
.main-nav__item:hover .main-nav__link {
  color: #d22856;
}
.main-nav__item:active .main-nav__link {
  color: rgba(210, 40, 86, 0.3);
}
.main-nav__link {
  color: #ffffff;
  text-decoration: none;
}
@media (min-width: 660px) {
  .main-nav {
    padding-top: 22px;
  }
}
@media (min-width: 1200px) {
  .main-nav {
    padding-top: 29px;
  }
  .main-nav--opened .main-nav__wrapper {
    position: static;
    width: 100%;
  }
  .main-nav--closed .main-nav__wrapper {
    display: block;
  }
  .main-nav__wrapper {
    vertical-align: middle;
  }
  .main-nav__toggle {
    display: none;
  }
  .main-nav__list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 620px;
    margin-left: auto;
  }
  .main-nav__item {
    min-width: auto;
    line-height: 18px;
    padding: 17px 0;
    background-color: transparent;
    border: none;
    box-shadow: none;
  }
  .main-nav__item:hover .main-nav__link {
    color: #d22856;
  }
  .main-nav__item:active .main-nav__link {
    color: rgba(255, 255, 255, 0.3);
  }
  .main-nav__item--active {
    position: relative;
  }
  .main-nav__item--active::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    height: 2px;
    background: #ffffff;
  }
  .main-nav__link {
    font-weight: 400;
    text-transform: none;
  }
}
